import React, { Component } from 'react'
import config from '../config'
import axios from 'axios'
import {Link, Redirect} from 'react-router-dom'


 class Myrequests extends Component {
    state = {
        requestcar: {},
        sportcars:{},
       
      }
    
      componentDidMount(){
        let sportcarId = this.props.match.params.sportcarId
        axios.get(`${config.API_URL}/api/requestcar/${sportcarId}`)
          .then((response) => {
            this.setState({ requestcar: response.data })
          })
          .catch(() => {
            
          })
      }
    
      render() {
        const {requestcar, sportcars} = this.state
        const {user} = this.props
        const {loggedInUser}= this.props
        
        return (
          <div>
           {/* <div> {sportcars.carName}</div> */}
            <h3> Requests</h3>
            <div> Date: {requestcar.date}</div>
            <div>Address: {requestcar.address}</div>
            
          </div>
        )
      }
}

export default Myrequests